
<template>
  <!-- 启动引导图 -->
    <div class="boot-page">
        <van-swipe class="boot-banner" @change="onChange" :show-indicators="false" :loop="false" :duration="260">
            <van-swipe-item class="swiper-slide" v-for="(item, index) in bootImgs" :key="index">
                <img :src="item.pic" >
                <div class="start-btn" :style="{backgroundImage: startBtnBackgroundImg}" @click="toApp" v-if="index == (bootImgs.length - 1) ?  true : false">立即开启</div>
            </van-swipe-item>
        </van-swipe>
        <!-- 指示器 -->
        <div class="custom-indicator">
            <span class="indicator-point" :style="{backgroundImage: indicatorBackImg(index)}" v-for="(item, index) in bootImgs" :key="index"></span>
        </div>
    </div>
</template>

<script>

    import Vue from 'vue';
    import {Toast, Swipe, SwipeItem} from 'vant';
    Vue.use(Toast).use(Swipe).use(SwipeItem);

    import {storage} from '../../utils/storage'
    import Config from "../../common/config";

    //引导图
    import bootImge1 from '@/assets/images/default/default1.png'
    import bootImge2 from '@/assets/images/default/default2.png'
    import bootImge3 from '@/assets/images/default/default3.png'
    //指示器图片未选择
    import indicatorNoSelectImg from '@/assets/images/default/default_bottom.png'
    //指示器图片已选择
    import indicatorSelectedImg from '@/assets/images/default/default_active.png'
    //开始按钮背景图
    import startBtnImg from '@/assets/images/default/default_btn_submit.png'


    export default {
        name: "bootPage",
        mounted () {
        },
        data() {
            return {
                title: '引导页',
                currentPage: 0,
                bootImgs : [
                    {pic: bootImge1},
                    {pic: bootImge2},
                    {pic: bootImge3}
                ],
                startBtnBackgroundImg: 'url('+ startBtnImg + ')',

                /// 本地图片
                bootImge1: bootImge1,
                bootImge2: bootImge2,
                bootImge3: bootImge3,
                indicatorNoSelectImg: indicatorNoSelectImg,
                indicatorSelectedImg: indicatorSelectedImg,
                startBtnImg: startBtnImg,
            };
        },
        methods: {
            onChange: function(index) {
                this.currentPage = index;
            },
            toApp: function() {

                //开启app
                storage.setStorage('appVersion', Config.version, 60);
                storage.setStorage('showBootPage', false, 60);
                
                if (this.$isLogin) {
                    //进入首页
                    this.$router.replace({name: "homePage"});
                } else {
                    //未登录进入登录页面
                    this.$router.replace({name: "login"});
                }
            }
        },
        computed: {
            indicatorBackImg() {
                return function(index) {
                    let img = this.currentPage == index ? 'url('+ indicatorSelectedImg + ')' :'url('+ indicatorNoSelectImg + ')';
                   return img;
                }
            }
        }
    };
</script>

<style scoped lang="scss">
    .boot-page {
        box-sizing: border-box;
        position: absolute;
        overflow: auto;
        width: 100%;
        height: 100%;
        background-color: #fff;
    }
  
    .boot-banner{
        height: 100%;
        width:100%;
        img {
            position: absolute;
            width: 100% !important;
            // height:100% !imporßtant;
            bottom: 20px;
        }
    }
    .start-btn{
        font-family: PingFangSC-Regular;
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0); 
        bottom: 11%;
        text-align: center;
        width: 200px;
        height: 56px;
        line-height: 56px;
        font-size: 15px;
        color: #ffffff;
        background-size: 100%;
        background-position: center;
        background-repeat: no-repeat; 
    }
    .custom-indicator {
        position: absolute;
        bottom: 7.6%;
        display: flex;
        left: 50%;
        transform: translate(-50%, 0);
        height: 20px;
        text-align: center;
        z-index:16;
        width: 50%;
        justify-content: center;
        .indicator-point {
            width: 13px;
            height: 20px;
            margin-right: 22px;
            background-size: 100%;
            background-position: center;
            background-repeat: no-repeat; 
        }
    }

</style>